import { Card, Row, Col, Segmented, Space } from "antd";
import React, { useLayoutEffect, useState } from "react";
import ProductImage from "@Components/ProductImage/ProductImage";
import {
  AppstoreOutlined,
  BarsOutlined,
  HeartTwoTone,
} from "@ant-design/icons";
import { useNavigate } from "react-router-dom";
import MockData from "@Mocks/MockData";

const App = () => {
  const navigate = useNavigate();

  const [productList] = useState(MockData.all);

  useLayoutEffect(function () {
    async function runAsync() {}
    runAsync();
  }, []);

  return (
    <>
      <Space direction="vertical" align="end" style={{ padding: 10 }}>
        <Segmented
          options={[
            {
              label: "列表",
              value: "列表",
              icon: <BarsOutlined />,
            },
            {
              label: "圖示",
              value: "圖示",
              icon: <AppstoreOutlined />,
            },
          ]}
        />
        <Row gutter={[16, 16]}>
          {productList.map((x, i) => {
            return (
              <Col span={12} key={i}>
                <Card
                  hoverable
                  cover={
                    <div style={{ height: "250px" }}>
                      <ProductImage src={x.image} />
                    </div>
                  }
                  onClick={() => {
                    navigate(`/product/${x.id}`);
                  }}
                  actions={[<HeartTwoTone twoToneColor="#d9bab6" />]}
                >
                  <Card.Meta title={x.name["zh-TW"]} description={`$${x.price}`} />
                </Card>
              </Col>
            );
          })}
        </Row>
      </Space>
    </>
  );
};

export default App;
